// API
fetch("https://psychive.microcms.io/api/v1/content", {
  headers: {
    "X-MICROCMS-API-KEY": "bc099eda05ad486897c3c5c23e2520e8f7b5"
  }
})
.then(function(res) {
    return res.json();
})
.then(function(json) {
    var fragment = document.createDocumentFragment();
    
    for (var i = 0; i < json.contents.length; i++) {
        // title
        var title = document.createElement('h1');
        title.textContent = json.contents[i].title;
        var url = '?lecture=' + title;
 
        // body
        var body = document.createElement('div');
        body.innerHTML = json.contents[i].body;
 
        var item = document.createElement('section');
        item.appendChild(title);
        item.appendChild(body);
        fragment.appendChild(item);
    }
    document.getElementById('lecture_list').appendChild(fragment);
})